/* global kakao */
import React, { useEffect } from "react";

const { kakao } = window;

const MapContainer = () => {
  //  
  useEffect(() => {
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(37.30016107093741, 127.04689796582203),
      level: 4,
    };
    const kakaoMap = new kakao.maps.Map(container, options);

    //   
    var markerPosition = new kakao.maps.LatLng(
      37.30016107093741,
      127.04689796582203
    );

    //  

    var marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    //     
    marker.setMap(kakaoMap);

    var iwContent =
        '<div style="padding:8px;"> <br><a href="https://map.kakao.com/?map_type=TYPE_MAP&target=car&rt=,,510301,1055817&rt1=&rt2=%EC%BD%94%EC%9C%84%EB%93%9C%EC%9B%90&rtIds=,11186485,37.30016107093741,127.04689796582203" style="color:blue" target="_blank"></a></div>', //    HTML  document element 
      // '<a href="https://map.kakao.com/?urlX=510301.0&amp;urlY=1055817.0&amp;itemId=11186485&amp;q=%EC%BD%94%EC%9C%84%EB%93%9C%EC%9B%90&amp;srcid=11186485&amp;map_type=TYPE_MAP&amp;from=roughmap" target="_blank"><span class="roughmap_lebel_text"></span></a>',
      iwPosition = new kakao.maps.LatLng(37.30016107093741, 127.04689796582203); //  

    //  
    var infowindow = new kakao.maps.InfoWindow({
      position: iwPosition,
      content: iwContent,
    });

    //    .   marker     
    infowindow.open(kakaoMap, marker);

    kakaoMap.setDraggable(false);
    // 
    kakaoMap.setZoomable(false);
  }, []);

  return (
    <div
      className="mx-auto w-full  py-16"
      // style={{
      //   width: "100%",
      //   display: "inline-block",
      //   marginLeft: "5px",
      //   marginRight: "5px",
      // }}
    >
      <div
        id="map"
        className="w-full h-[400px] lg:h-[500px]"
        // style={{ width: "99%", height: "500px" }}
      ></div>
    </div>
  );
};

export default MapContainer;
